<template>
  <div class="sckviewer">
    <div class="devlist" v-if="devices">
      <ul>
        <li v-for="(item,index) in devices" :key="index">
          <div>
            <p>
              <span v-if="item.deviceType === 0">未定义类型设备</span>
              <span v-if="item.deviceType === 1">智能终端</span>
              <span v-if="item.deviceType === 2">PC</span>
              <span v-if="item.deviceType === 3">IP电话</span>
              <span v-if="item.deviceType === 4">摄像头</span>
              <span v-if="item.deviceType === 5">编码卡</span>
              <span v-if="item.deviceType === 6">服务器</span>
              <span v-if="item.deviceType === 7">IC卡读卡器</span>
              <span v-if="item.deviceType === 8">485物联设备</span>
              <span v-if="item.deviceType === 9">录播主机</span>
              <span v-if="item.deviceType === 10">无线麦</span>
              <span v-if="item.deviceType === 11">投影仪</span>
              <span v-if="item.deviceType === 12">门禁主机</span>
              <span v-if="item.deviceType === 13">电源箱</span>
              <span v-if="item.deviceType === 14">音箱</span>
              <span v-if="item.deviceType === 15">LED屏</span>
              <span v-if="item.deviceType === 20">遥控宝</span>
              <span v-if="item.deviceType === 21">遥控宝-红外</span>
              <span v-if="item.deviceType === 22">遥控宝-风扇控制器</span>
              <span v-if="item.deviceType === 23">遥控宝-窗帘控制器</span>
              <span v-if="item.deviceType === 24">遥控宝-开关面板</span>
              <span v-if="item.deviceType === 30">情景面板</span>
              <span v-if="item.deviceType === 40">睿和物联网网关</span>
              <span v-if="item.deviceType === 41">睿和单开面板</span>
              <span v-if="item.deviceType === 42">睿和双开面板</span>
              <span v-if="item.deviceType === 43">睿和三开面板</span>
              <span v-if="item.deviceType === 44">睿和调光器</span>
              <span v-if="item.deviceType === 45">睿和红外转发器</span>
              <span v-if="item.deviceType === 46">睿和调速器</span>
              <span v-if="item.deviceType === 47">睿和窗帘控制器</span>
              <span v-if="item.deviceType === 48">睿和原版红外转发器</span>
              <span>
                <el-button
                  v-if="item.deviceType === 1 ||
                   item.deviceType === 2 ||
                   item.deviceType === 3 ||
                   item.deviceType === 4 ||
                   item.deviceType === 5 ||
                   item.deviceType === 7 ||
                   item.deviceType === 8 ||
                   item.deviceType === 12 ||
                   item.deviceType === 15"
                  type="primary"
                  size="mini"
                  @click="EditBtn(item)"
                >设置</el-button>
                <el-button type="primary" size="mini" @click="DviceHandle(item)">报修</el-button>
              </span>
            </p>
            <p>
              <span>品牌：{{item.brandModelName}}</span>
              <span>名称：{{item.deviceName}}</span>
            </p>
            <p>
              <span>序列号：{{item.deviceSerialNo}}</span>
              <span>
                状态：
                <span style="color:#e6a23c" v-if="item.expiry === 0">未设置日期</span>
                <span style="color:#F56C6C" v-if="item.expiry === 2">过保</span>
                <span style="color:#67c23a" v-if="item.expiry === 1">在保</span>
              </span>
            </p>
          </div>
        </li>
      </ul>
    </div>
    <div v-if="!devices" class="none">
      <p>无设备</p>
    </div>
    <DeviceDlg
      :isModal="false"
      :DeviceType.sync="DeviceType"
      :JcName.sync="JcName"
      :ModuleName.sync="ModuleName"
      :RecordBoolean.sync="RecordBoolean"
      :ActiveTab.sync="ActiveTab"
      :Devicedatas.sync="Devicedatas"
      :Dcxinxi.sync="Dcxinxi"
      :ID.sync="ID"
      :Drlist.sync="Drlist"
      :ParamBoolean.sync="ParamBoolean"
      :DialogVisible.sync="DialogVisible"
      @load="load"
    />
    <Create
      :isModal="false"
      :visible.sync="Cvisible"
      @load="refresh"
      :Did.sync="Did"
      :Cid.sync="Cid"
      @close="close"
    ></Create>
  </div>
</template>

<script>
import moment from "moment";
import api from "@/scripts/api";
export default {
  components: {
    Create: () => import("@/components/Device/CreateRepair"),
    DeviceDlg: () => import("@/components/Device/DeviceDlg")
  },
  props: {},
  data() {
    return {
      devices: null,
      Cvisible: false,
      Did: "",
      Cid: "",
      DialogVisible: false,
      ID: "",
      DeviceType: -1,
      JcName: true,
      ModuleName: "设备",
      RecordBoolean: true,
      ActiveTab: "all",
      Devicedatas: {
        DeviceType: "",
        DeviceName: "",
        ModelID: "",
        ClsRoomID: "",
        ControlIP: "",
        ControlPort: "",
        ControlMAC: "",
        ControlGateway: "",
        ControlSubNetMask: "",
        ControlVer: ""
      },
      Dcxinxi: {},
      Drlist: [],
      ParamBoolean: false,
      clasroomid: "",
      clsid: "",
      deviceid: "",
      ppxh: ""
    };
  },
  mounted() {
    this.LoadData();
  },
  methods: {
    async LoadData() {
      const roomid = this.$parent.$route.params.id;
      const res = await api.Device.GetDeviceByClsRoomID(roomid);
      if (res.Datas && res.Datas.length > 0) {
        this.devices = [];
        res.Datas.forEach(element => {
          var expiry = 0;
          if (element.device.DeviceExpiryDate) {
            if (
              moment(element.device.DeviceExpiryDate).format("x") <
              new Date(new Date().toLocaleDateString()).getTime()
            ) {
              expiry = 2;
            } else {
              expiry = 1;
            }
          }
          this.devices.push({
            deviceID: element.device.DeviceID,
            roomID: element.device.RoomID,
            deviceName: element.device.DeviceName,
            deviceType: element.device.DeviceType,
            deviceSerialNo: element.device.DeviceSerialNo,
            brandModelName:
              element.brandName && element.modelName
                ? element.brandName + "(" + element.modelName + ")"
                : "",
            expiry: expiry
          });
        });
      }
    },
    async close() {
      this.Did = "";
      this.Cid = "";
      this.Cvisible = false;
    },
    refresh() {
      this.LoadData();
    },
    async DviceHandle(row) {
      this.Did = row.deviceID;
      this.Cid = row.roomID;
      this.Cvisible = true;
    },
    async load() {
      this.LoadData();
      this.clasroomid = "";
      this.clsid = "";
      this.deviceid = "";
      this.ppxh = "";
    },
    /**
     * 设置
     */
    async EditBtn(row) {
      this.ActiveTab = "all";
      this.DeviceType = -1;
      this.ParamBoolean = true;
      this.RecordBoolean = true;
      this.JcName = false;
      this.DialogVisible = true;
      const res = await api.Device.GetDeviceOperRecords(row.deviceID);
      if (res.Datas) {
        this.Drlist = res.Datas;
      } // 历史记录
      if (row.deviceType !== -1) {
        const res = await api.Device.GetData(row.deviceID);
        if (res) {
          this.Dcxinxi = res.Data;
        }
      }
      if (this.deviceType === -1) {
        const res = await api.Device.GetData(row.deviceID);
        if (res) {
          res.Data.DeviceType = -1;
          res.Data.Devicezdy = row.DeviceType;
          this.ID = res.Data.Key.DeviceID;
          this.Devicedatas = res.Data;
        }
      } // 终端设备// 全部设备
      if (row.deviceType === 1) {
        const res = await api.Ctrl.GetData(row.deviceID);
        if (res) {
          res.Data.DeviceType = 1;
          this.ID = res.Data.DeviceID;
          this.Devicedatas = res.Data;
          this.DeviceType = 1;
        }
      } // 终端设备
      if (row.deviceType === 4) {
        const res = await api.Camera.GetData(row.deviceID);
        if (res) {
          if (res.Data !== null) {
            res.Data.DeviceType = 4;
            this.ID = res.Data.Key.DeviceID;
            this.Devicedatas = res.Data;
            this.DeviceType = 4;
          }
        }
      } // 摄像头
      if (row.deviceType === 5) {
        const res = await api.VideoCard.GetData(row.deviceID);
        if (res) {
          res.Data.DeviceType = 5;
          this.ID = res.Data.Key.DeviceID;
          this.Devicedatas = res.Data;
          this.DeviceType = 5;
        }
      } // 课件编码器
      if (row.deviceType === 3) {
        const res = await api.IPPhone.GetData(row.deviceID);
        if (res) {
          res.Data.DeviceType = 3;
          this.ID = res.Data.Key.DeviceID;
          this.Devicedatas = res.Data;
          this.DeviceType = 3;
        }
      } // IP电话
      if (row.deviceType === 2) {
        const res = await api.PC.GetData(row.deviceID);
        if (res) {
          res.Data.DeviceType = 2;
          this.ID = res.Data.Key.DeviceID;
          this.Devicedatas = res.Data;
          this.DeviceType = 2;
        }
      } // PC
      if (row.deviceType === 7) {
        const res = await api.ICCard.GetData(row.deviceID);
        if (res) {
          res.Data.DeviceType = 7;
          this.ID = res.Data.Key.DeviceID;
          this.Devicedatas = res.Data;
          this.DeviceType = 7;
        }
      } // IC卡读卡器
      if (row.deviceType === 11) {
        const res = await api.Projector.GetData(row.deviceID);
        if (res) {
          res.Data.DeviceType = 11;
          this.ID = res.Data.Key.DeviceID;
          this.Devicedatas = res.Data;
          this.DeviceType = 11;
        }
      } // 投影仪
      if (row.deviceType === 8) {
        const res = await api.IOT.GetData(row.deviceID);
        if (res) {
          res.Data.DeviceType = 8;
          this.ID = res.Data.Key.DeviceID;
          this.Devicedatas = res.Data;
          this.DeviceType = 8;
        }
      } // 485物联网设备
      if (row.deviceType === 12) {
        const res = await api.EGurad.GetData(row.deviceID);
        if (res) {
          res.Data.DeviceType = 12;
          this.ID = res.Data.Key.DeviceID;
          this.Devicedatas = res.Data;
          this.DeviceType = 12;
        }
      } // 门禁主机
      if (row.deviceType === 15) {
        const res = await api.LED.GetData(row.deviceID);
        if (res) {
          res.Data.DeviceType = 15;
          this.ID = res.Data.Key.DeviceID;
          this.Devicedatas = res.Data;
          this.DeviceType = 15;
        }
      } // 门禁主机
    }
  }
};
</script>

<style lang="less">
.sckviewer {
  .devlist {
    height: calc(100% - 20px);
    overflow: hidden;
    overflow-y: auto;

    li {
      height: 80px;
      margin-bottom: 10px;
      display: grid;
      grid-template-columns: auto;

      > div {
        display: grid;
        grid-template-columns: auto;
        grid-template-rows: repeat(3);
      }

      > div.over {
        background: #b9bfc5;
      }

      > div:last-child {
        padding: 5px 10px;
        color: #333333;
        background-color: #f8f8f8;

        > p {
          display: flex;
          justify-content: space-between;
        }

        > p:first-child {
          font-size: 15px;
          font-weight: 600;
        }
      }
    }

    li:last-child {
      margin-bottom: 0px;
    }
  }
  .none {
    > p {
      font-size: 48px;
      text-align: center;
      line-height: 300px;
      color: #c7cdd3;
    }
  }
}
</style>