<template>
  <div>
    <el-row>
      <el-col :span="12">
        <el-form-item label="计划名称：">{{Info.RPName}}</el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item label="计划类型：">
          <span v-if="Info.RPType === 1">教师</span>
          <span v-if="Info.RPType === 2">课程</span>
          <span v-if="Info.RPType === 3">专业</span>
          <span v-if="Info.RPType === 4">班级</span>
          <span v-if="Info.RPType === 5">年级</span>
          <span v-if="Info.RPType === 6">教室</span>
          <span v-if="Info.RPType === 7">指定课节</span>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="12">
        <el-form-item label="计划时间：" v-if="Info.RPType === 7">{{planTime}}</el-form-item>
        <el-form-item
          v-if="Info.RPType !== 7&&isView"
          label="计划时间："
        >{{Info.RPStart[0] | FormatDate}} 至 {{Info.RPStart[1] | FormatDate}}</el-form-item>
        <el-form-item
          v-if="Info.RPType !== 7&&!isView"
          label="计划时间："
        >{{Info.RPStart[0] }} 至 {{Info.RPStart[1] }}</el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item label="是否直播：">{{Info.RPLive ? "是" : "否"}}</el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="Info.RPType === 6 ? 12 : 24">
        <el-form-item label="计划对象：">{{Info.RPTName}}</el-form-item>
      </el-col>
      <el-col :span="12" v-if="Info.RPType === 6">
        <el-form-item label="计划模式：">
          <span v-if="Info.RPMode === 1">按课表</span>
          <span v-if="Info.RPMode === 2">按课节</span>
          <span v-if="Info.RPMode === 3">全天录制</span>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24" v-if="!(Info.RPType === 7 || (Info.RPType === 6 && Info.RPMode !== 1))">
        <el-form-item label="排除项：">
          <el-table stripe height="200px" :data="Info.ExItems">
            <el-table-column width="70" property="typeid" label="排除类型">
              <template slot-scope="scope">
                <span v-if="scope.row.typeid === 1">教师</span>
                <span v-if="scope.row.typeid === 2">课程</span>
                <span v-if="scope.row.typeid === 3">专业</span>
                <span v-if="scope.row.typeid === 4">班级</span>
                <span v-if="scope.row.typeid === 5">年级</span>
                <span v-if="scope.row.typeid === 6">教室</span>
                <span v-if="scope.row.typeid === 7">指定课节</span>
              </template>
            </el-table-column>
            <el-table-column property="name" label="排除对象" />
            <el-table-column width="180" property="date" label="排除时间"></el-table-column>
          </el-table>
        </el-form-item>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  props: {
    // 信息
    Info: {
      type: Object,
      default: () => null
    },
    isView: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    planTime () {
      const a = this.Info.RPTName.match(/\d{4}-\d{1,2}-\d{1,2}/);
      const b = this.Info.RPTName.match(
        /\d{1,2}:\d{1,2}:\d{1,2}-\d{1,2}:\d{1,2}:\d{1,2}/
      );

      if (a && b) {
        return a[0] + " " + b[0];
      } else {
        return "--";
      }
    }
  }
};
</script>

<style lang="less">
</style>